export default function fotoInit() {
    const selectors = {
        main: '.b-foto',
        slider: '.b-foto__slider'
    };

    const carouselOptions = {
        loop: true,
        margin: 25,
        nav: true,
        navText: ['', ''],
        mouseDrag: false,
        lazyLoad:true,
        responsive : {
            0 : {
                items: 1,
            },
            480 : {
                items: 2
            },
            800 : {
                items: 3
            },
            1200 : {
                items: 4
            }
        }
    };

    $(`${selectors.main}.js-init ${selectors.slider}`).owlCarousel(carouselOptions)
}
