export default function faqSliderInit(){
    const classes = {
        main: 'b-faq-slider',
        slider: 'b-faq-slider__slider'
    };

    const owlSettings = {
        loop: true,
        nav: false,
        responsive: {
            0: {
                items: 1,
            },
            750: {
                items: 2,
            }
        }
    };

    const owlCarousel = $(`.${classes.main}.js-init .${classes.slider}`).owlCarousel(owlSettings)
}
