export default function dDMenuInit() {
    const selectors = {
        main: '.b-dropdown-menu',
        selectCity: '.b-dropdown-menu__select-city',
        detectCity: '.b-dropdown-menu__detect-city',
        cityName: '.b-dropdown-menu__city-name'
    };

    const classes = {
        selectCityVisible: 'b-dropdown-menu__select-city_visible',
    };

    $(selectors.main).each((i, elem) => {
        const $elem = $(elem);
        if(!$elem.hasClass('js-init')) return;

        initDDMenu($elem);
    });

    function initDDMenu($elem){
        const elems = {
            $main: $elem,
            $selectCity: $elem.find(selectors.selectCity),
            $detectCity: $elem.find(selectors.detectCity),
            $cityName: $elem.find(selectors.cityName)
        };

        elems.$cityName.click(() => {
            elems.$selectCity.slideToggle(() => {
                elems.$selectCity.toggleClass(classes.selectCityVisible);
            });
        });

        $(document).mouseup(function (e) {
            if (elems.$selectCity.has(e.target).length === 0
                && !elems.$cityName.is(e.target)
                && elems.$cityName.has(e.target).length === 0
            ){
                elems.$selectCity.slideUp();
            }
        })
    }
}
