import { declinationNumber } from '../../../resources/scripts/_tools'

export default class Input{
    static COUNTER_TYPES = {
        HOURS: 'hours',
        MINUTES: 'minutes'
    }

    static selectors = {
        main: '.b-input',
        input: '.b-input__input',
        counterUiMinus: '.b-input__counter-ui_minus',
        counterUiPlus: '.b-input__counter-ui_plus',
        calendarIcon: '.b-input__calendar',
        eye: '.b-input__eye',
        inputWrapper: '.b-input__input-wrapper',
        inputValue: '.b-input__input_value',
        inputView: '.b-input__input_view',
    };

    static init($context = $(document), options = {}) {
        $(Input.selectors.main, $context).each((i, elem) => {
            const $elem = $(elem);

            if( !$elem.hasClass('js-init') ) return;
            if( $elem.data('block-api') ) return;

            new Input($elem, options);
        })
    }

    constructor($elem, options) {
        this.params = $.extend({
            selectors: Input.selectors,
        }, options);

        this.elems = {
            $main: $elem,
            $input: $elem.find(this.params.selectors.input),
            counterUiMinus: $elem.find(this.params.selectors.counterUiMinus),
            counterUiPlus: $elem.find(this.params.selectors.counterUiPlus),
            calendarIcon: $elem.find(this.params.selectors.calendarIcon),
            $eyes: $elem.find(this.params.selectors.eye),
            $inputWrapper: $elem.find(this.params.selectors.inputWrapper),
            $inputValue: $elem.find(this.params.selectors.inputValue),
            $inputView: $elem.find(this.params.selectors.inputView),
        };

        this.state = {
            defaultNumber: true
        };

        this.params.type = this.elems.$main.data('input-type');
        this.params.config = this.elems.$main.data('config') || {};

        this.startByInputType();

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    getCounterValueTextByType(newValue) {
        const { renderType } = this.params.config;

        switch (renderType) {
            case Input.COUNTER_TYPES.HOURS:
                return `${newValue} ${declinationNumber(newValue, ['час', 'часа', 'часов'])}`
            case Input.COUNTER_TYPES.MINUTES:
                return `${newValue} ${declinationNumber(newValue, ['минута', 'минуты', 'минут'])}`
            default:
                return newValue
        }
    }

    renderCounterValueByType(newValue) {
        const { renderType } = this.params.config;

        switch (renderType) {
            case Input.COUNTER_TYPES.HOURS:
            case Input.COUNTER_TYPES.MINUTES:
                this.elems.$inputView.val(newValue)
                break;
            default:
                this.elems.$input.val(newValue)
        }
    }

    handleCounter() {
        const minValue = this.params.config.minValue || 0;
        const maxValue = this.params.config.maxValue || Infinity;
        const currentValue = +this.elems.$input.val();

        this.renderCounterValueByType(this.getCounterValueTextByType(currentValue));

        this.elems.counterUiPlus.click(() => {
            const currentValue = +this.elems.$input.val();
            if( currentValue < maxValue ){
                const newValue = currentValue + 1;
                this.elems.$input.val(newValue);
                this.renderCounterValueByType(this.getCounterValueTextByType(newValue));
                this.elems.$input.change();
            }
        });

        this.elems.counterUiMinus.click(() => {
            const currentValue = +this.elems.$input.val();
            if( currentValue > minValue ){
                const newValue = currentValue - 1
                this.elems.$input.val(newValue);
                this.renderCounterValueByType(this.getCounterValueTextByType(newValue));
                this.elems.$input.change();
            }
        });

        this.elems.$input.keydown((e) => {
            const approvedKeys = ['Backspace', 'Enter', 'ArrowLeft', 'ArrowRight', 'Delete'];
            const pattern = /[0-9]/;
            if(!pattern.test(e.key) && !approvedKeys.find( key => key === e.key )){
                e.preventDefault();
            } else if(pattern.test(e.key)){
                if(this.state.defaultNumber){
                    this.elems.$input.val(e.key);
                    this.state.defaultNumber = false;
                } else {
                    this.elems.$input.val(this.elems.$input.val() + e.key);
                }
                e.preventDefault();
            }
        })
    }

    handleDatepicker() {
        this.elems.$input.datepicker({
            autoClose: true,
            classes: this.elems.$main.data('datepicker-class') || ''
        });

        this.datepicker = this.elems.$input.datepicker().data('datepicker');

        this.elems.calendarIcon.click(() => {
            this.datepicker.show();
        })
    }

    handlePhone() {
        this.elems.$input.mask("+7(999) 999-99-99");
    }

    handleDateMask() {
        this.elems.$input.mask("99.99.9999");
    }

    handlePassword() {
        this.elems.$eyes.click(e => {
            this.elems.$input.attr('type', this.elems.$input.attr('type') === 'password' ? 'text' : 'password')
            this.elems.$eyes.removeClass('u-hide');
            $(e.currentTarget).addClass('u-hide');
        })
    }

    startByInputType() {
        if (!this.params.type) return false;

        switch(this.params.type){
            case 'counter':
                this.handleCounter();
                break;
            case 'datepicker':
                this.handleDatepicker();
                break;
            case 'phone':
                this.handlePhone();
                break;
            case 'password':
                this.handlePassword();
                break;
            case 'date-mask':
                this.handleDateMask();
                break;
            default:
                break;
        }
    }
}
