
export default class Rating{
    static get selectors(){
        return{
            main: '.b-rating',
            starItem: '.b-rating__star-holder',
            starImgFilled: '.b-rating__star-img_filled',
            starImgHalf: '.b-rating__star-img_half'
        }
    }

    static get classes(){
        return{
            starImgActive: 'b-rating__star-img_active',
        }
    }

    static get dataSelectors(){
        return{
            rating: 'rating',
            blockApi: 'block-api'
        }
    }

    static init($context = $(document), options){
        $(Rating.selectors.main, $context).each((index, elem) => {
            const $elem = $(elem);

            if(!$elem.hasClass('js-init')) return;
            if($elem.data(Rating.dataSelectors.blockApi)) return;

            new Rating($elem, options);
        })
    }

    constructor($elem, options){
        this.params = $.extend({
            selectors: Rating.selectors,
        }, options);

        this.elems = {
            $main: $elem,
            $starItem: $elem.find(this.params.selectors.starItem),
            $starImgFilled: $elem.find(this.params.selectors.starImgFilled),
            $starImgHalf: $elem.find(this.params.selectors.starImgHalf),
        };

        this.state = {
            rating: this.elems.$main.data(Rating.dataSelectors.rating) || 0
        };

        this.renderRating();

        this.initBlockApi();
    }

    initBlockApi(){
        this.elems.$main.data(Rating.dataSelectors.blockApi, this);
    }

    fillStar($item, fillMod){
        const $star = fillMod === 'half' ?
            $item.find(this.elems.$starImgHalf)
            : $item.find(this.elems.$starImgFilled);

        $star.addClass(Rating.classes.starImgActive);
    }

    renderRating(rating = this.state.rating){
        this.elems.$starImgFilled.removeClass(Rating.classes.starImgActive);
        this.elems.$starImgHalf.removeClass(Rating.classes.starImgActive);

        this.elems.$starItem.each((index, elem) => {

            const differ = rating - index;
            if(differ >= 1){
                this.fillStar($(elem))
            } else {
                if(differ >= 0.5){
                    this.fillStar($(elem), 'half')
                } else return;
            }

        })
    }

}