import Tools from '../../../resources/scripts/_tools';
import { ButtonPreloader } from '../../../resources/scripts/_tools';

const BLOCK_TYPES = {
    INPUT_TEXT: 'input-text',
    INPUT_SELECT: 'input-select',
    INPUT_RADIO: 'input-radio',
    TEXTAREA: 'textarea'
};

const INPUT_NAMES = {
  PHONE: 'popup_phone'
};

const REQUEST_SETTINGS = {
    URL: '/landing/action/send_form',
    TYPE: 'POST',
};

export default class TypeForm{
    static selectors = {
        main: '.b-type-form',
        stepImg: '.b-type-form__step-img',
        formScreen: '.b-type-form__form-screen',
        inputBlock: '.b-type-form__input-block',
        dialogText: '.b-type-form__dialog-text',
        input: '.b-input__input',
        textarea: '.b-textarea__textarea',
        jsGoNext: '.js-tf-go-next',
        bInput: '.b-input',
        bInputSelect: '.b-input-select',
        bInputRadio: '.b-input-radio',
        bTextarea: '.b-textarea',
        viewport: '.b-type-form__form-viewport',
        canvas: '.b-type-form__viewport-canvas',
        prevBtn: '.b-type-form__prev-btn',
        jsUiItem: '.js-tf-ui-item',
        error: '.b-type-form__error',
        submit: '.js-tf-submit',
        form: '.b-type-form__form',
        popup: '.b-popup',
        jsDemo: 'js-demo',
        consultBtn: '.b-type-form__consult-btn',
        token: 'meta[name="csrf-token"]'
    };

    static dataSelectors = {
        dialogText: 'tf-dialog-step',
        stepImg: 'tf-step-img',
        screen: 'tf-screen',
        blockType: 'block-type',
        required: 'required',
        additionalGroup: 'tf-additional-gr',
        additionalBtn: 'tf-additional-btn',
        stopEnterChange: 'tf-stop-enter-change',
        popupConsult: 'popup-consult',
        popupSuccess: 'popup-success',
        cookieNames: 'tf-cookie-names',
        cookieLabel: 'tf-cookie-label'
    };

    static classes = {
        dialogTextActive: 'b-type-form__dialog-text_active',
        stepImgVisible: 'b-type-form__step-img_visible',
        formScreenActive: 'b-type-form__form-screen_active',
        inputBlockActive: 'b-type-form__input-block_active',
        inputBlockVisible: 'b-type-form__input-block_visible',
        inputBlockOpacityHalf: 'b-type-form__input-block_opacity_half',
        inputBlockOpacityMin: 'b-type-form__input-block_opacity_min',
        bInputFocus: 'b-input_focus-theme_shadow-green',
        bInputSelectFocus: 'b-input-select_focus-theme_shadow-green',
        bInputRadioFocus: 'b-input-radio_focus-theme_shadow-green',
        bTextareaFocus: 'b-textarea_focus-theme_shadow-green',
        canvasSliding: 'b-type-form__viewport-canvas_sliding',
        prevBtnVisible: 'b-type-form__prev-btn_visible'
    };

    static init($context = $(document), options = {}) {
        $(TypeForm.selectors.main, $context).each((i, elem) => {
            const $elem = $(elem);

            if(!$elem.hasClass('js-init')) return;
            if($elem.data('block-api')) return;

            new TypeForm($elem, options);
        })
    }

    constructor($elem, options) {
        const cookieDefaults = [
            'popupModel',
            'popupWeight',
            'popupSterilized',
            'popupPovodok',
            'popupPasport',
            'aggr',
            'text1',
            'text2',
            'text3',
        ];

        const cookieLabelDefault = 'tfFields';

        this.params = $.extend({
            selectors: TypeForm.selectors,
            dataSelectors: TypeForm.dataSelectors,
            animateDuration: 300,
            visibleBlocks: 2,
        }, options);

        this.elems = {
            $main: $elem,
            $stepImg: $elem.find(this.params.selectors.stepImg),
            $formScreen: $elem.find(this.params.selectors.formScreen),
            $inputBlock: $elem.find(this.params.selectors.inputBlock),
            $dialogText: $elem.find(this.params.selectors.dialogText),
            $jsGoNext: $elem.find(this.params.selectors.jsGoNext),
            $viewport: $elem.find(this.params.selectors.viewport),
            $canvas: $elem.find(this.params.selectors.canvas),
            $prevBtn: $elem.find(this.params.selectors.prevBtn),
            $input: $elem.find(this.params.selectors.input),
            $submitBtn: $elem.find(this.params.selectors.submit),
            $form: $elem.find(this.params.selectors.form),
            $consultBtn: $elem.find(this.params.selectors.consultBtn)
        };

        this.params.popupsId = {
            consult: this.elems.$main.data(TypeForm.dataSelectors.popupConsult),
            success: this.elems.$main.data(TypeForm.dataSelectors.popupSuccess)
        };

        this.params.cookieNames = this.elems.$main.data(TypeForm.dataSelectors.cookieNames) || cookieDefaults;
        this.params.cookieLabel = this.elems.$main.data(TypeForm.dataSelectors.cookieLabel) || cookieLabelDefault;

        this.state = {
          isActive: false,
          translate: 0,
          activeAddGr: 0,
          sending: false
        };

        this.initSlider();

        this.initBlockApi();
    }

    slide($targetSlide, direction){
        const slideDiffer = this.elems.$viewport.offset().top - $targetSlide.offset().top;

        this.state.translate += slideDiffer;
        this.elems.$canvas.css({'-webkit-transform': `translate(0, ${this.state.translate}px)`});

        this.elems.$canvas.addClass(TypeForm.classes.canvasSliding);
        setTimeout(() => {

            this.elems.$canvas.removeClass(TypeForm.classes.canvasSliding);
            this.setFocusToInputBlock($targetSlide);
            this.chooseDialogText($targetSlide.data(TypeForm.dataSelectors.screen));
            this.chooseStepImage($targetSlide.data(TypeForm.dataSelectors.screen));

            this.elems.$inputBlock.index($targetSlide) > 0 ?
                this.elems.$prevBtn.addClass(TypeForm.classes.prevBtnVisible)
                : this.elems.$prevBtn.removeClass(TypeForm.classes.prevBtnVisible)

        }, this.params.animateDuration);

        $targetSlide.addClass(TypeForm.classes.inputBlockActive);
        $targetSlide.removeClass(TypeForm.classes.inputBlockOpacityMin);
        $targetSlide.removeClass(TypeForm.classes.inputBlockOpacityHalf);
        this.applyOpacities($targetSlide);
        this.chooseVisibleBlocks($targetSlide);
    }

    applyOpacities($activeBlock){
        this.elems.$inputBlock.not($activeBlock)
            .removeClass(TypeForm.classes.inputBlockOpacityHalf)
            .addClass(TypeForm.classes.inputBlockOpacityMin);
        $activeBlock.next()
            .removeClass(TypeForm.classes.inputBlockOpacityMin)
            .addClass(TypeForm.classes.inputBlockOpacityHalf);
    }

    handleSlide(direction = 'next'){
        const $activeBlock = this.elems.$inputBlock.filter(`.${TypeForm.classes.inputBlockActive}`).eq(0);
        if($activeBlock.length > 0 && direction === 'next' && !this.checkRequires($activeBlock)){
            this.error($activeBlock);
            return;
        }
        const $targetBlock = direction === 'next' ? $activeBlock.next(this.elems.$inputBlock) : $activeBlock.prev(this.elems.$inputBlock)
        if($targetBlock.length > 0 && !this.elems.$canvas.hasClass(TypeForm.classes.canvasSliding)){
            $activeBlock.removeClass(TypeForm.classes.inputBlockActive);
            this.slide($targetBlock);
        }
    }

    initSlider() {
        const $starSlide = this.elems.$inputBlock.eq(0);
        this.slide($starSlide);
        this.handleBlocksChange();
        this.handleResize();
        this.handleUiItemsChange();
        this.handleInputKeyPress();
        this.handleSubmit();
        this.handleConsultClick();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    chooseDialogText(screenId) {
        let newDialogText = null;
        this.elems.$dialogText.each((i, elem) => {
            if($(elem).data(this.params.dataSelectors.dialogText) === screenId){
                newDialogText = $(elem);
                return false;
            }
        });

        this.elems.$dialogText.removeClass(TypeForm.classes.dialogTextActive);
        if(newDialogText === null){
            this.elems.$main
                .find(`${this.params.selectors.dialogText}[data-${this.params.dataSelectors.dialogText}=0]`)
                .addClass(TypeForm.classes.dialogTextActive)
        } else {
            newDialogText.addClass(TypeForm.classes.dialogTextActive);
        }
    }

    chooseStepImage(screenId) {
        let newStepImage;
        this.elems.$stepImg.each((i, elem) => {
            if($(elem).data(this.params.dataSelectors.stepImg) === screenId){
                newStepImage = $(elem);
                this.elems.$stepImg.removeClass(TypeForm.classes.stepImgVisible);
                newStepImage.addClass(TypeForm.classes.stepImgVisible);
                return false;
            }
        })
    }

    chooseVisibleBlocks($activeBlock){
        const activeBlockIndex = this.elems.$inputBlock.index($activeBlock);
        this.elems.$inputBlock.removeClass(TypeForm.classes.inputBlockVisible);
        const nextVisibleBlocks = this.elems.$inputBlock.filter((index) => {
            return (index > activeBlockIndex)
                && (index <= activeBlockIndex + this.params.visibleBlocks)
        });

        nextVisibleBlocks.filter((index, block) => {
            const additionalGroupId = $(block).data(this.params.dataSelectors.additionalGroup);
            if(additionalGroupId){
                return this.state.activeAddGr >= additionalGroupId
            } else return true;
        }).addClass(TypeForm.classes.inputBlockVisible)
    }

    setFocusToInputText($formBlock) {
        $formBlock.find(this.params.selectors.bInput).addClass(TypeForm.classes.bInputFocus);
        if(this.state.isActive){
            $formBlock.find(this.params.selectors.input).focus();
        }
    }

    setFocusToInputSelect($formBlock) {
        $formBlock.find(this.params.selectors.bInputSelect).addClass(TypeForm.classes.bInputSelectFocus)
    }

    setFocusToInputRadio($formBlock) {
        $formBlock.find(this.params.selectors.bInputRadio).addClass(TypeForm.classes.bInputRadioFocus)
    }

    setFocusToTextarea($formBlock) {
        $formBlock.find(this.params.selectors.bTextarea).addClass(TypeForm.classes.bTextareaFocus)
        if(this.state.isActive){
            $formBlock.find(this.params.selectors.textarea).focus();
        }
    }

    setFocusToInputBlock($formBlock) {
        const blockType = $formBlock.data(this.params.dataSelectors.blockType);

        switch (blockType) {
            case BLOCK_TYPES.INPUT_TEXT:
                this.setFocusToInputText($formBlock);
                break;
            case BLOCK_TYPES.INPUT_SELECT:
                this.setFocusToInputSelect($formBlock);
                break;
            case BLOCK_TYPES.INPUT_RADIO:
                this.setFocusToInputRadio($formBlock);
                break;
            case BLOCK_TYPES.TEXTAREA:
                this.setFocusToTextarea($formBlock);
                break;
            default:
                break;
        }
    }

    checkRequires($formBlock) {
        if(!$formBlock.data(TypeForm.dataSelectors.required)) return true;

        const blockType = $formBlock.data(TypeForm.dataSelectors.blockType);

        switch (blockType) {
            case BLOCK_TYPES.INPUT_TEXT:
                return $formBlock.find(this.params.selectors.input).val().length > 0;
            default:
                return true;
        }
    }

    handleBlocksChange() {
        const keyPressCodes = [13];
        const keyPressBlockCodes = [9];

        this.elems.$main.click(() => {
            if( !this.state.isActive ){
                this.state.isActive = true;
            }
        });

        $(document).keyup(e => {
            if( keyPressCodes.find( keyCode => keyCode === e.keyCode ) && this.state.isActive ) {
                const $activeBlock = $(this.elems.$inputBlock.filter(`.${TypeForm.classes.inputBlockActive}`));
                if(!$activeBlock.data(this.params.dataSelectors.stopEnterChange)){
                    this.handleSlide();
                }
                else{
                    this.handleUiBlock($activeBlock);
                }
            }
        });

        this.elems.$inputBlock.keydown((e) => {
            if( keyPressBlockCodes.find( keyCode => keyCode === e.keyCode ) ) {
                e.preventDefault();
            }
        });
        this.elems.$jsGoNext.click((e) => {
            const additionalGrBtn = $(e.currentTarget).data(this.params.dataSelectors.additionalBtn);
            if(additionalGrBtn){
                this.state = {
                    ...this.state,
                    activeAddGr: additionalGrBtn
                }
            }
            this.handleSlide();
        });
        this.elems.$prevBtn.click(() => {
            this.handleSlide('prev');
        })
    }

    handleResize(){
        $(window).resize(() => {
            if(!this.elems.$canvas.hasClass(TypeForm.classes.canvasSliding)){
                this.slide(this.elems.$inputBlock.filter(`.${TypeForm.classes.inputBlockActive}`).eq(0));
            }
        })
    }

    handleUiBlock($block){
        const $uiItems = $block.find(this.params.selectors.jsUiItem);

        if($uiItems.filter(':focus').length === 0){
            $uiItems.eq(0).focus();
        }
    }

    changeUiItem(changeDirection = 'next'){

        switch (changeDirection) {
            case 'next':
                this.elems.$main.find(`${this.params.selectors.jsUiItem}:focus`)
                    .next(this.params.selectors.jsUiItem)
                    .focus();
                break;
            case 'prev':
                this.elems.$main.find(`${this.params.selectors.jsUiItem}:focus`)
                    .prev(this.params.selectors.jsUiItem)
                    .focus();
                break;
            default:
                break;
        }
    }

    handleUiItemsChange(){
        $(document).keyup((event) => {
            const keyCodes = {
                prev: [37],
                next: [39]
            }

            if(keyCodes.prev.find(item => item === event.keyCode)){
                this.changeUiItem('prev');
            }
            if(keyCodes.next.find(item => item === event.keyCode)){
                this.changeUiItem('next');
            }

        })
    }

    handleInputKeyPress() {
        this.elems.$input.keypress(e => {
            this.error($(e.currentTarget).closest(this.elems.$inputBlock), 'hide');
        })
    }

    error($block, toggler = 'show') {
        switch (toggler){
            case 'show':
                $block.find(this.params.selectors.error).show();
                break;
            case 'hide':
                $block.find(this.params.selectors.error).hide();
                break;
            default:
                break;
        }
    }

    convertPhone(phoneStr){
        return Number(phoneStr.replace(/\D+/g,""));
    }

    submitDemo(data){
        setTimeout(() => {
            ButtonPreloader.hidePreloader(this.elems.$submitBtn);
            this.state = {
                ...this.state,
                sending: false
            };
            Tools.closePopup();
            Tools.openPopup(this.params.popupsId.success);
            this.cookieHandler(data);
            console.log(data);
        }, 2000)
    }

    submit(data) {
        const request = $.ajax({
            headers: {
                'X-REQUEST-PAGE': $('meta[name="request_page"]').attr('content')
            },
            type: REQUEST_SETTINGS.TYPE,
            url: REQUEST_SETTINGS.URL,
            data
        })
            .always(() => {
                this.state = {
                    ...this.state,
                    sending: false
                };
                ButtonPreloader.hidePreloader(this.elems.$submitBtn)
            })
            .done((responseData) => {
                if(responseData.operation_status){
                    if(responseData.operation_status.status === 'success'){
                        Tools.closePopup();
                        Tools.openPopup(this.params.popupsId.success);
                        this.cookieHandler(data);
                    } else {
                        alert(responseData.operation_status.message)
                    }
                }
            })
            .fail(() => {
                alert('Ошибка отправки формы. Попробуйте еще раз');
            })

        return request;
    }

    handleSubmit() {
        this.elems.$submitBtn.click(() => {
            if(!this.state.sending){

                this.state = {
                    ...this.state,
                    sending: true
                };
                ButtonPreloader.showPreloader(this.elems.$submitBtn)

                const data = this.elems.$form.serializeArray()
                    .map(field => {
                        if(field.name === INPUT_NAMES.PHONE){
                            return {
                                ...field,
                                value: this.convertPhone(field.value)
                            }
                        }
                        return field;
                    });

                data.push({
                    name: '_token',
                    value: $(this.params.selectors.token).attr('content')
                });

                if(this.elems.$main.hasClass(this.params.selectors.jsDemo)){
                    this.submitDemo(data);
                } else this.submit(data);

            }
        })
    }

    handleConsultClick(){
        this.elems.$consultBtn.click(() => {

            Tools.closePopup();
            Tools.openPopup(this.params.popupsId.consult);
        })
    }

    cookieHandler(sandedData){
        const cookieValues = sandedData.filter( dataItem => {
            return $.trim(dataItem.value) && this.params.cookieNames.find( cookieName => cookieName === dataItem.name )
        } );

        if(cookieValues.length > 0){
            Tools.setCookie(this.params.cookieLabel, JSON.stringify(cookieValues));
        }

    }
}
