export default function initServiceOrderForm () {

    const togglerText = {
        open: 'ПРОСТАЯ ФОРМА ЗАКАЗА',
        close: 'РАСШИРЕННАЯ ФОРМА ЗАКАЗА'
    }

    const selectors = {
        main: '.b-service-order',
        dopFormToggler: '.b-service-order__dop-form-toggler',
        togglerTextHolder: '.b-service-order__form-toggler-text',
        dopFormWrapper: '.b-service-order__dop-form-wrapper'
    }

    const classes = {
        dopFormVisible: 'b-service-order_dop-form-visible'
    }

    const init = ($elem) => {
        const $elements = {
            $main: $elem,
            $dopFormToggler: $elem.find(selectors.dopFormToggler),
            $togglerTextHolder: $elem.find(selectors.togglerTextHolder),
            $dopFormWrapper: $elem.find(selectors.dopFormWrapper)
        }

        $elements.$dopFormToggler.click(() => {
            $elements.$main.toggleClass(classes.dopFormVisible)
            $elements.$dopFormWrapper.slideToggle();
            $elements.$togglerTextHolder.text($elements.$main.hasClass(classes.dopFormVisible) ? togglerText.open : togglerText.close)
        })
    }

    $(selectors.main).each((index, elem) => {
        const $elem = $(elem);

        init($elem)
    })
}