import { tns } from 'tiny-slider/src/tiny-slider';

export default function comixSliderInit() {
    const selectors = {
        main: '.b-comix-slider',
        slider: '.b-comix-slider__slider',
        pagination: '.b-comix-slider__pagination',
        slide: '.b-comix-slider__slide',
        arrowsContainer: '.b-comix-slider__arrows',
        arrowPrev: '.b-comix-slider__arrow_prev',
        arrowNext: '.b-comix-slider__arrow_next',
    };

    const classes = {
        arrowHide: 'b-comix-slider__arrow_hide'
    };

    const carouselOptions = {
        items: 1,
        slideBy: 'page',
        autoplay: false,
        mouseDrag: true,
        navPosition: 'bottom',
        controlsPosition: 'bottom',
        lazyload: true,
        rewind: true,
    };

    const renderArrows = (elems, slider) => {
        const info = slider.getInfo();

        if(info.index === 0){
            elems.$arrowPrev.addClass(classes.arrowHide);
        } else {
            elems.$arrowPrev.removeClass(classes.arrowHide);
        }
        if(info.index === info.pages - 1){
            elems.$arrowNext.addClass(classes.arrowHide);
        } else {
            elems.$arrowNext.removeClass(classes.arrowHide);
        }
    }


    $(selectors.main).each( (i, elem) => {
        const $elem = $(elem);
        const elems = {
            $main: $elem,
            $slider: $elem.find(selectors.slider),
            $pagination: $elem.find(selectors.pagination),
            $slide: $elem.find(selectors.slide),
            $arrowsContainer: $elem.find(selectors.arrowsContainer),
            $arrowPrev: $elem.find(selectors.arrowPrev),
            $arrowNext: $elem.find(selectors.arrowNext),
        };

        for(let i=0; i < elems.$slide.length; ++i) {
            const decoratedPage = i < 9 ? `0${i+1}` : i+1;
            elems.$pagination.append(`<span class="b-comix-slider__page"><span class="b-comix-slider__page-number">${decoratedPage}</span></span>`)
        }

        const slider = tns({
            ...carouselOptions,
            container: elems.$slider.get(0),
            navContainer: elems.$pagination.get(0),
            controlsContainer: elems.$arrowsContainer.get(0),
            preventScrollOnTouch: 'auto'
        });

        if(slider) {
            renderArrows(elems, slider);
            slider.events.on('indexChanged', () => {
                renderArrows(elems, slider);
            })
        }


    })

}