export default function employeesSliderInit() {
    const slickSettings = {
        slidesToShow: 3,
        vertical: true,
        verticalSwiping: true,
        infinite: false,
        responsive: [
            {
                breakpoint: 600,
                settings: {
                    vertical: false
                }
            }
        ]
    };

    const selectors = {
        main: '.b-employees-slider',
        slider: '.b-employees-slider__slider',
        slide: '.b-employees-slider__slide',
        tab: '.b-employees-slider__tab'
    };
    const classes = {
        slideActive: 'b-employees-slider__slide_active',
        tabActive: 'b-employees-slider__tab_visible'
    };
    const dataSelectors = {
        tabTargetId: 'emp'
    };

    $(selectors.main).each((i, elem) => {
        if(!$(elem).hasClass('js-init')) return;
        init($(elem));
    });

    function init($elem) {
        const elems = {
            $main: $elem,
            $slider: $elem.find(selectors.slider),
            $slide: $elem.find(selectors.slide),
            $tab: $elem.find(selectors.tab)
        };

        elems.$slider.slick(slickSettings);

        elems.$slide.click((e) => {
            const $slide = $(e.currentTarget);
            elems.$slide.removeClass(classes.slideActive);
            $slide.addClass(classes.slideActive);
            const targetId = $slide.data(dataSelectors.tabTargetId);
            changeTab({elems, tabSelector: `#${targetId}`})
        });

        changeTab({elems});
    }

    function changeTab({elems, tabSelector}){
        const $targetTab = tabSelector === undefined ? elems.$tab.eq(0) : $(tabSelector);
        elems.$tab.removeClass(classes.tabActive);
        $targetTab.addClass(classes.tabActive);
    }
}