import Popup from "../b-popup/b-popup";

export default function initTestimonial() {
    const selectors = {
        main: '.b-testimonial',
        moreBtn: '.b-testimonial__more-btn',
        title: '.b-testimonial__title',
        text: '.b-testimonial__text-content',
        authorImg: '.b-testimonial__author-img',
        authorName: '.b-testimonial__author-name',
        authorDescript: '.b-testimonial__author-descript',
        socIcon: '.b-testimonial__soc-img',
        socLabel: '.b-testimonial__soc-label'
    }

    const classes = {
        jsInit: 'js-dynamic-popup'
    }

    const getElems = ($elem) => {
        return {
            $main: $elem,
            $moreBtn: $(selectors.moreBtn, $elem),
            $title: $(selectors.title, $elem),
            $text: $(selectors.text, $elem),
            $authorImg: $(selectors.authorImg, $elem),
            $authorName: $(selectors.authorName, $elem),
            $authorDescript: $(selectors.authorDescript, $elem),
            $socIcon: $(selectors.socIcon, $elem),
            $socLabel: $(selectors.socLabel, $elem),
        }
    }

    $(selectors.main + `.${classes.jsInit}`).each((index, elem) => {
        const $elem = $(elem);

        const $popup = $(`#${$elem.data('popup-target')}`)
        const popupElems = getElems($popup)

        const testimonialElems = getElems($elem)

        testimonialElems.$moreBtn.click(() => {
            popupElems.$title.text(testimonialElems.$title.text().trim())
            popupElems.$text.text(testimonialElems.$text.text().trim())
            popupElems.$authorImg.attr('src', testimonialElems.$authorImg.attr('src'))
            popupElems.$authorName.text(testimonialElems.$authorName.text().trim())
            popupElems.$authorDescript.text(testimonialElems.$authorDescript.text().trim())
            popupElems.$socIcon.attr('src', testimonialElems.$socIcon.attr('src'))
            popupElems.$socLabel.text(testimonialElems.$socLabel.text().trim())

            Popup.open(popupElems.$main);

        })
    })
}