
export default class Calculator{

    static PACKS = {
        FREE: 'free',
        FRIENDSHIP: 'friendship',
        GULDOG: 'guldog',
        BIG: 'big',
        WALKS_15: 'walks-15',
        WALKS_30: 'walks-30',
        FRIENDSHIP_ACTIVE: 'friendship-active',
        GULDOG_ACTIVE: 'guldog-active',
        BIG_ACTIVE: 'big-active',
        SITTING_VIGUL: 'sitting-vigul',
        GULDOG_SITTING: 'guldog-sitting',
        BIG_SITTING: 'big-sitting',
        SITTING_DRESS: 'sitting-dress',
        GULDOG_SITTING_DRESS: 'guldog-sitting-dress',
        BIG_SITTING_DRESS: 'big-sitting-dress',
        CONSULTATION: 'consultation',
        GULDOG_CONSULTATION: 'guldog-consultation',
        BIG_CONSULTATION: 'big-consultation',
        FRIENDSHIP_DRESSURA: 'friendship-dressura',
        GULDOG_DRESSURA: 'guldog-dressura',
        BIG_DRESSURA: 'big-dressura',
        DAYTIME_NANNY: 'daytime-nanny',
        GULDOG_NANNY: 'guldog-nanny',
        BIG_NANNY: 'big-nanny',
        DAY_NANNY: 'day-nanny'
    }

    static get selectors() {
        return {
            main: '.b-calculator',
            uiWrapper: '.b-calculator__ui-wrapper',
            totalPayPeriod: '.b-calculator__pay-period',
            summaryFrom: '.b-calculator__summary-from',
            summaryValute: '.b-calculator__summary-valute',
            packItem: '.js-pack-item',
            blockCalculatorPack: '.b-calculator-pack',
            uiCounterWalkTime: '.js-ui-counter-walk-time',
            uiPetsNumber: '.js-ui-pets-number',
            uiCounterWalkNumber: '.js-ui-counter-walk-number',
            uiDopCollar: '.js-ui-dop-collar',
            totalValue: '.js-summary-value',
            uiSittingDaysNumber: '.js-ui-counter-sittings-number',
            uiConsultationsNumber: '.js-ui-counter-consultation-number',
            uiCounterDaysNumber: '.js-ui-counter-days-number',
            packsTab: '.js-packs-tab',
            tabWrapper: '.js-packs-tab-wrapper',
            uiResultValueMod: '.js-ui-result-value-mod'
        }
    }

    static get classes() {
        return {
            uiWrapperActive: 'b-calculator__ui-wrapper_active',
            blockCalculatorPackChecked: 'b-calculator-pack_color-scheme_checked',
            jsPackItemActive: 'js-pack-item_active',
            summaryFromHide: 'b-calculator__summary-from_hide',
            summaryValuteHide: 'b-calculator__summary-valute_hide',
            packsTabWrapperHide: 'b-calculator__packs-list_hidden'
        }
    }

    static get dataSelectors() {
        return {
            packTarget: 'pack-target',
            packsTabTarget: 'packs-tab-target',
            resultValueMod: 'result-value-mod'
        }
    }

    static init($context = $(document), options) {
        $(Calculator.selectors.main, $context).each((index, elem) => {
            const $elem = $(elem);
            if(!$elem.hasClass('js-init') || $elem.data('block-api')) return;

            new Calculator($elem, options);
        })
    }

    constructor($elem, options) {
        this.params = {
            selectors: Calculator.selectors,
            classes: Calculator.classes,
            dataSelectors: Calculator.dataSelectors,
            ...options
        }

        this.elems = {
            $main: $elem,
            $packItems: $elem.find(this.params.selectors.packItem),
            $uiWrappers: $elem.find(this.params.selectors.uiWrapper),
            $blocksCalculatorPack: $elem.find(this.params.selectors.blockCalculatorPack),
            $totalPayPeriod: $elem.find(this.params.selectors.totalPayPeriod),
            $totalValue: $elem.find(this.params.selectors.totalValue),
            $summaryFrom: $elem.find(this.params.selectors.summaryFrom),
            $summaryValute: $elem.find(this.params.selectors.summaryValute),
            $packsTabs: $elem.find(this.params.selectors.packsTab),
            $packsTabWrapper: $elem.find(this.params.selectors.tabWrapper),
            $uiResultValueMod: $elem.find(this.params.selectors.uiResultValueMod)
        }

        this.calcTotal();
        this.handlePackChange();
        this.handleUiChange();
        this.handleTabsChange();

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    renderPackChange($newPackElem) {
        const packName = $newPackElem.data(this.params.dataSelectors.packTarget);
        const $blockCalculatorPackElem = $newPackElem.find(this.params.selectors.blockCalculatorPack)

        this.elems.$uiWrappers.removeClass(this.params.classes.uiWrapperActive)
        this.elems.$uiWrappers
            .filter(`${this.params.selectors.uiWrapper}_pack_${packName}`)
            .addClass(this.params.classes.uiWrapperActive);
        this.elems.$packItems.find(this.elems.$blocksCalculatorPack).removeClass(this.params.classes.blockCalculatorPackChecked)
        this.elems.$packItems.removeClass(this.params.classes.jsPackItemActive)
        $newPackElem.addClass(this.params.classes.jsPackItemActive)
        $blockCalculatorPackElem.addClass(this.params.classes.blockCalculatorPackChecked)
    }

    renderTotalPayPeriodByPackName(packName) {
        let payPeriod = '';

        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)
        const daysNumber = Number($uiWrapperActive.find(this.params.selectors.uiCounterDaysNumber).val());

        switch (packName) {
            case Calculator.PACKS.GULDOG:
            case Calculator.PACKS.BIG:
            case Calculator.PACKS.WALKS_15:
            case Calculator.PACKS.WALKS_30:
            case Calculator.PACKS.GULDOG_DRESSURA:
            case Calculator.PACKS.BIG_DRESSURA:
                payPeriod = '/выгул';
                break;
            case Calculator.PACKS.GULDOG_SITTING:
            case Calculator.PACKS.BIG_SITTING:
            case Calculator.PACKS.SITTING_DRESS:
            case Calculator.PACKS.GULDOG_SITTING_DRESS:
            case Calculator.PACKS.BIG_SITTING_DRESS:
                payPeriod = '/сутки'
                break;
            case Calculator.PACKS.GULDOG_NANNY:
            case Calculator.PACKS.BIG_NANNY:
                payPeriod = '/визит'
                break;
            case Calculator.PACKS.DAY_NANNY:
                if(daysNumber === 1) payPeriod = '/сутки'
                break;
        }

        this.elems.$totalPayPeriod.text(payPeriod)

    }

    handlePackChange() {
        this.elems.$packItems.click((e) => {
            const $clickedPack = $(e.currentTarget);
            this.renderPackChange($clickedPack);
            this.calcTotal();
        })
    }

    handleUiChange() {
        $(`${this.params.selectors.uiCounterWalkTime},
            ${this.params.selectors.uiPetsNumber},
            ${this.params.selectors.uiCounterWalkNumber},
            ${this.params.selectors.uiDopCollar},
            ${this.params.selectors.uiSittingDaysNumber},
            ${this.params.selectors.uiConsultationsNumber},
            ${this.params.selectors.uiCounterDaysNumber},
            ${this.params.selectors.uiResultValueMod}`, this.elems.$main).change(() => {
                this.calcTotal();
        })
    }

    handleTabsChange() {
        this.elems.$packsTabs.click((event) => {
            const $newTabElem = $(event.currentTarget);
            const newTabTargetName = $newTabElem.data(this.params.dataSelectors.packsTabTarget)
            const $newTabWrapperElem = this.elems.$packsTabWrapper
                .filter(`.b-calculator__packs-list_tab_${newTabTargetName}`)

            this.elems.$packsTabWrapper.addClass(this.params.classes.packsTabWrapperHide)
            $newTabWrapperElem.removeClass(this.params.classes.packsTabWrapperHide)
            $newTabElem.removeClass(this.params.classes.packsTabWrapperHide)

            this.elems.$packsTabs.find(this.elems.$blocksCalculatorPack).removeClass(this.params.classes.blockCalculatorPackChecked)
            $newTabElem.find(this.elems.$blocksCalculatorPack).addClass(this.params.classes.blockCalculatorPackChecked)

            if(!$newTabElem.hasClass(this.params.selectors.packItem.split('.')[1])) {
                this.renderPackChange($newTabWrapperElem.find(this.params.selectors.packItem).eq(0));
                this.calcTotal();
            }
        })
    }

    getResultValueModKF() {
        return this.elems.$uiResultValueMod.length > 0 && this.elems.$uiResultValueMod.is(":checked") ?
            this.elems.$uiResultValueMod.data(Calculator.dataSelectors.resultValueMod) || null
            : null
    }

    calcTotal() {
        const activePack = this.elems.$packItems
            .filter(`.${this.params.classes.jsPackItemActive}`)
            .data(this.params.dataSelectors.packTarget)

        const resultValueMod = this.getResultValueModKF();
        const resultValueByPackName = this.getTotalValueByPackName(activePack);

        const total = resultValueMod ?
            Math.floor(resultValueByPackName * resultValueMod)
            : resultValueByPackName

        this.elems.$totalValue.text( total.toLocaleString('ru-RU') )
        this.renderTotalDopText(activePack);
        this.renderTotalPayPeriodByPackName(activePack);
    }

    getTotalValueByPackName(packName) {
        switch (packName){
            case Calculator.PACKS.FREE:
                return 'Бесплатно'
            case Calculator.PACKS.FRIENDSHIP:
                return this.getTotalValueForPackFriendship()
            case Calculator.PACKS.GULDOG:
                return this.getTotalValueForPackGuldog()
            case Calculator.PACKS.BIG:
                return this.getTotalValueForPackGuldog(529)
            case Calculator.PACKS.WALKS_15:
                return this.getTotalValueForPackGuldog(390)
            case Calculator.PACKS.WALKS_30:
                return this.getTotalValueForPackGuldog(370)
            case Calculator.PACKS.FRIENDSHIP_ACTIVE:
                return this.getTotalValueForPackFriendshipActive()
            case Calculator.PACKS.GULDOG_ACTIVE:
                return this.getTotalValueForPackFriendshipActive(845)
            case Calculator.PACKS.BIG_ACTIVE:
                return this.getTotalValueForPackFriendshipActive(800)
            case Calculator.PACKS.SITTING_VIGUL:
                return this.getTotalValueForPackSitting()
            case Calculator.PACKS.GULDOG_SITTING:
                return this.getTotalValueForPackSittingPerDay()
            case Calculator.PACKS.BIG_SITTING:
                return this.getTotalValueForPackSittingPerDay(709)
            case Calculator.PACKS.SITTING_DRESS:
                return this.getTotalValueForPackSitting(1700)
            case Calculator.PACKS.GULDOG_SITTING_DRESS:
                return this.getTotalValueForPackSittingPerDay(1625)
            case Calculator.PACKS.BIG_SITTING_DRESS:
                return this.getTotalValueForPackSittingPerDay(1550)
            case Calculator.PACKS.CONSULTATION:
                return this.getTotalValueForPackConsultation()
            case Calculator.PACKS.GULDOG_CONSULTATION:
                return this.getTotalValueForPackConsultation(1235)
            case Calculator.PACKS.BIG_CONSULTATION:
                return this.getTotalValueForPackConsultation(1170)
            case Calculator.PACKS.FRIENDSHIP_DRESSURA:
                return this.getTotalValueForPackDressura()
            case Calculator.PACKS.GULDOG_DRESSURA:
                return this.getTotalValueForPackDressuraWalk()
            case Calculator.PACKS.BIG_DRESSURA:
                return this.getTotalValueForPackDressuraWalk(890)
            case Calculator.PACKS.DAYTIME_NANNY:
                return this.getTotalValueForPackNanny()
            case Calculator.PACKS.GULDOG_NANNY:
                return this.getTotalValueForPackNannyPerVisit()
            case Calculator.PACKS.BIG_NANNY:
                return this.getTotalValueForPackNannyPerVisit(585)
            case Calculator.PACKS.DAY_NANNY:
                return this.getTotalValueForPackNannyDay()
            default:
                return 0
        }
    }

    getTotalValueForPackFriendship(basePrice = 589) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const walkTime = $uiWrapperActive.find(this.params.selectors.uiCounterWalkTime).val();
        const walkTimeSummator = walkTime > 1 ? 300 * (walkTime - 1) : 0

        const petsNumber = $uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val();
        const petsNumberSummator = petsNumber > 1 ? 200 * (petsNumber - 1) : 0

        const walkNumber = $uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val();

        const dopCollarValue = $uiWrapperActive.find(this.params.selectors.uiDopCollar).val();
        const isDopCollarChecked = $uiWrapperActive.find(this.params.selectors.uiDopCollar).is(':checked');
        const dopCollarSummator = isDopCollarChecked ? dopCollarValue * petsNumber : 0

        return (basePrice + walkTimeSummator + petsNumberSummator + dopCollarSummator) * walkNumber
    }

    getTotalValueForPackGuldog(basePrice = 559) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const petsNumber = $uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val();
        const petsNumberSummator = petsNumber > 1 ? 200 * (petsNumber - 1) : 0

        const dopCollarValue = $uiWrapperActive.find(this.params.selectors.uiDopCollar).val();
        const isDopCollarChecked = $uiWrapperActive.find(this.params.selectors.uiDopCollar).is(':checked');
        const dopCollarSummator = isDopCollarChecked ? dopCollarValue * petsNumber : 0

        return basePrice + petsNumberSummator + dopCollarSummator
    }

    getTotalValueForPackFriendshipActive(basePrice = 890) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const walkNumber = $uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val();

        const dopCollarValue = $uiWrapperActive.find(this.params.selectors.uiDopCollar).val();
        const isDopCollarChecked = $uiWrapperActive.find(this.params.selectors.uiDopCollar).is(':checked');
        const dopCollarSummator = isDopCollarChecked ? Number(dopCollarValue) : 0

        return (basePrice + dopCollarSummator) * walkNumber
    }

    getTotalValueForPackSitting(basePrice = 790) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const sittingDaysNumber = $uiWrapperActive.find(this.params.selectors.uiSittingDaysNumber).val();

        const walkNumber = $uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val();
        const walkNumberSummator = basePrice + 200 * (walkNumber - 1)

        const petsNumber = $uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val();
        const petsNumberSummator = petsNumber > 1 ?
                (400 + (walkNumber > 1 ?
                    200 * (walkNumber - 1)
                    : 0))
                * (petsNumber - 1)
            : 0

        return (walkNumberSummator + petsNumberSummator) * sittingDaysNumber
    }

    getTotalValueForPackSittingPerDay(basePrice = 749) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const walkNumber = $uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val();
        const walkNumberSummator = basePrice + 200 * (walkNumber - 1)

        const petsNumber = $uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val();
        const petsNumberSummator = petsNumber > 1 ?
            (400 + (walkNumber > 1 ?
                200 * (walkNumber - 1)
                : 0))
            * (petsNumber - 1)
            : 0

        return walkNumberSummator + petsNumberSummator;
    }

    getTotalValueForPackConsultation(basePrice = 1300) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)
        const consultationsNumber = Number($uiWrapperActive.find(this.params.selectors.uiConsultationsNumber).val());

        return basePrice * consultationsNumber;
    }

    getTotalValueForPackDressura(basePrice = 990) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const walkTime = $uiWrapperActive.find(this.params.selectors.uiCounterWalkTime).val();
        const walkTimeSummator = walkTime > 1 ? 200 * (walkTime - 1) : 0

        const walkNumber = $uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val();

        const dopCollarValue = Number($uiWrapperActive.find(this.params.selectors.uiDopCollar).val());
        const isDopCollarChecked = $uiWrapperActive.find(this.params.selectors.uiDopCollar).is(':checked');
        const dopCollarSummator = isDopCollarChecked ? dopCollarValue : 0

        return (basePrice + walkTimeSummator + dopCollarSummator) * walkNumber
    }

    getTotalValueForPackDressuraWalk(basePrice = 940) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const dopCollarValue = Number($uiWrapperActive.find(this.params.selectors.uiDopCollar).val());
        const isDopCollarChecked = $uiWrapperActive.find(this.params.selectors.uiDopCollar).is(':checked');
        const dopCollarSummator = isDopCollarChecked ? dopCollarValue : 0

        return basePrice + dopCollarSummator;
    }

    getTotalValueForPackNanny(basePrice = 650) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const walkTime = Number($uiWrapperActive.find(this.params.selectors.uiCounterWalkTime).val());
        const walkTimeSummator = walkTime > 1 ? 220 * (walkTime - 1) : 0

        const petsNumber = Number($uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val());
        const petsNumberSummator = petsNumber > 1 ? 200 * (petsNumber - 1) * walkTime : 0

        const walkNumber = Number($uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val());
        const walkNumberSummator = walkNumber * 250  * petsNumber;


        return basePrice + walkTimeSummator + petsNumberSummator + walkNumberSummator;
    }

    getTotalValueForPackNannyPerVisit(basePrice = 615) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const petsNumber = Number($uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val());
        const petsNumberSummator = petsNumber > 1 ? 200 * (petsNumber - 1) : 0

        const walkNumber = Number($uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val());
        const walkNumberSummator = walkNumber * 250  * petsNumber;


        return basePrice + petsNumberSummator + walkNumberSummator;
    }

    getTotalValueForPackNannyDay(basePrice = 1950) {
        const $uiWrapperActive = this.elems.$uiWrappers.filter(`.${this.params.classes.uiWrapperActive}`)

        const daysNumber = Number($uiWrapperActive.find(this.params.selectors.uiCounterDaysNumber).val());

        const petsNumber = Number($uiWrapperActive.find(this.params.selectors.uiPetsNumber).filter(':checked').val());
        const petsNumberSummator = petsNumber > 1 ? 200 * (petsNumber - 1) : 0

        const walkNumber = Number($uiWrapperActive.find(this.params.selectors.uiCounterWalkNumber).val());
        const walkNumberSummator = walkNumber * 250 * petsNumber;

        return (basePrice + petsNumberSummator) * daysNumber + walkNumberSummator;
    }

    renderTotalDopText(activePack) {
        if(activePack === Calculator.PACKS.FREE) {
            this.elems.$summaryFrom.addClass(this.params.classes.summaryFromHide);
            this.elems.$summaryValute.addClass(this.params.classes.summaryValuteHide);
        } else {
            this.elems.$summaryFrom.removeClass(this.params.classes.summaryFromHide);
            this.elems.$summaryValute.removeClass(this.params.classes.summaryValuteHide);
        }
    }
}