
export default class GallerySlider {
    static get selectors() {
        return {
            context: '.js-gallery-slider',
            mainViewport: '.b-gallery-slider__slides',
            navCarousel: '.b-gallery-slider__nav-carousel',
            arrowNext: '.b-gallery-slider__arrow_next',
            arrowPrev: '.b-gallery-slider__arrow_prev',
            arrowNextMain: '.b-gallery-slider__arrow-main_next',
            arrowPrevMain: '.b-gallery-slider__arrow-main_prev',
        }
    }

    static defaults = {
        mobileBreakpoint: 600
    };

    static init($context = $(document), options = {}) {
        $(GallerySlider.selectors.context, $context).each((i, elem) => {
            if($(elem).data('block-api')) return;

            new GallerySlider($(elem), options);
        })
    }

    constructor($elem, options) {
        this.params = $.extend({
            selectors: GallerySlider.selectors,
            defaults: GallerySlider.defaults,
            configMain: $elem.data('config-main') || {},
            configPreview: $elem.data('config-preview') || {}
        }, options);

        this.elems = {
            $main: $elem,
            $mainViewport: $elem.find(this.params.selectors.mainViewport),
            $navCarousel: $elem.find(this.params.selectors.navCarousel),
            $arrowNext: $elem.find(this.params.selectors.arrowNext),
            $arrowPrev: $elem.find(this.params.selectors.arrowPrev),
            $arrowNextMain: $elem.find(this.params.selectors.arrowNextMain),
            $arrowPrevMain: $elem.find(this.params.selectors.arrowPrevMain)
        };

        this.initMainSlider();
        this.initNavCarousel();

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    initMainSlider() {
        const configMainDefault = {
            slidesToShow: 1,
            slidesToScroll: 1,
            fade: true,
            asNavFor: this.elems.$navCarousel,
            nextArrow: this.elems.$arrowNextMain,
            prevArrow: this.elems.$arrowPrevMain
        };

        this.elems.$mainViewport.slick({
            ...configMainDefault,
            ...this.params.configMain
        })
    }

    initNavCarousel() {
        const configPreviewDefault = {
            slidesToShow: 4,
            slidesToScroll: 1,
            vertical: true,
            asNavFor: this.elems.$mainViewport,
            nextArrow: this.elems.$arrowPrev,
            prevArrow: this.elems.$arrowNext,
            focusOnSelect: true,

        };

        this.elems.$navCarousel.slick({
            ...configPreviewDefault,
            ...this.params.configPreview
        })
    }

    handleResize() {
        $(window).resize(() => {
            const windowWidth = $(window).width();
            if( windowWidth > this.params.defaults.mobileBreakpoint ){
                this.initNavCarousel();
            }
        })
    }
}
