import { tns } from 'tiny-slider/src/tiny-slider';

export default function initColumnCarousel() {
    const selectors = {
        main: '.b-column-carousel',
        carousel: '.b-column-carousel__carousel',
        arrowsContainer: '.b-column-carousel__arrows',
        arrowPrev: '.b-column-carousel__arrow_prev',
        arrowNext: '.b-column-carousel__arrow_next',
        pages: '.b-column-carousel__pages',
        slide: '.b-column-carousel__col'
    }

    const colsPerPageByWindowSize = [
        {size: 0, perPage: 1},
        {size: 853, perPage: 2},
        {size: 1047, perPage: 3},
    ]

    const renderPagesByWindowSize = (elems) => {
        const windowWidth = Number($(window).width());
        let bpInfo = colsPerPageByWindowSize.find((breakpoint, index, array) => {
            return ( (windowWidth >= breakpoint.size) && (index === array.length - 1) )
                || ( (windowWidth >= breakpoint.size) && (windowWidth < array[index + 1].size) )
        })

        if(bpInfo === undefined) bpInfo = {}

        elems.pages.empty();
        for(let i=0; i < Math.ceil(elems.slides.length / bpInfo.perPage); ++i) {
            const decoratedPage = i < 9 ? `0${i+1}` : i+1;
            elems.pages.append(`<span class="b-column-carousel__page"><span class="b-column-carousel__page-number">${decoratedPage}</span></span>`)
        }
    }

    $(selectors.main).each((index, elem) => {
        const $elem = $(elem);
        const elems = {
            main: $elem,
            carousel: $elem.find(selectors.carousel),
            arrowsContainer: $elem.find(selectors.arrowsContainer),
            arrowPrev: $elem.find(selectors.arrowPrev),
            arrowNext: $elem.find(selectors.arrowNext),
            pages: $elem.find(selectors.pages),
            slides: $elem.find(selectors.slide)
        }

        const carouselOptions = {
            items: 3,
            slideBy: 'page',
            autoplay: false,
            mouseDrag: true,
            navPosition: 'bottom',
            controlsPosition: 'bottom',
            lazyload: false,
            rewind: true,
            autoHeight: true,
            responsive: {
                320: {
                    items: 1
                },
                853: {
                    items: 2
                },
                1047: {
                    items: 3
                }
            }
        };

        renderPagesByWindowSize(elems);

        let carousel = tns({
            ...carouselOptions,
            container: elems.carousel.get(0),
            navContainer: elems.pages.get(0),
            controlsContainer: elems.arrowsContainer.get(0),
            preventScrollOnTouch: 'auto'
        });

        $(window).on('resize', () => {
            renderPagesByWindowSize(elems);
            if(carousel.destroy) {
                carousel.destroy();
                carousel.rebuild()
            }
        })

    })

}