export default class MobileMenu{
    static selectors = {
        main: '.b-mobile-menu',
        selectCity: '.b-mobile-menu__select-city',
        currentCity: '.b-mobile-menu__current-city',
        closeBtn: '.b-mobile-menu__close-btn, .js-mobile-menu-hide-menu',
        menuParentBlock: '.b-menu__mobile-menu',
        menu: '.b-menu'
    }

    static classes = {
        visible: 'b-mobile-menu_visible',
        selectCityVisible: 'b-mobile-menu__select-city_visible',
        menuParentBlockVisible: 'b-menu__mobile-menu_visible',
        mobileMenuVisible: 'b-menu_mobile-visible',
    }

    static init($context = $(document), options = {}) {
        $(MobileMenu.selectors.main).each((i, elem) => {
            const $elem = $(elem);

            if(!$elem.hasClass('js-init')) return;
            if($elem.data('block-api')) return;

            new MobileMenu($elem, options);
        })
    }

    constructor($elem, options) {
        this.params = $.extend({
            selectors: MobileMenu.selectors,
            classes: MobileMenu.classes
        }, options)

        this.elems = {
            $main: $elem,
            $selectCity: $elem.find(this.params.selectors.selectCity),
            $currentCity: $elem.find(this.params.selectors.currentCity),
            $closeBtn: $elem.find(this.params.selectors.closeBtn),
            $menuParentBlock: $elem.closest(this.params.selectors.menuParentBlock),
            $menu: $elem.closest(this.params.selectors.menu)
        }

        this.handleSelectCity();
        this.handleClose();

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    handleSelectCity() {
        this.elems.$currentCity.click(() => {
            this.elems.$selectCity
                .slideToggle(() => {
                    this.elems.$selectCity.toggleClass(this.params.classes.selectCityVisible)
                })
        })
    }

    handleClose() {
        this.elems.$closeBtn.click(() => {
            this.elems.$main.removeClass(this.params.classes.visible)
            this.elems.$menuParentBlock.removeClass(this.params.classes.menuParentBlockVisible)
            this.elems.$menu.removeClass(this.params.classes.mobileMenuVisible)
        })
    }
}
