import CountdownTimer from 'countdown';

const selectors = {
    main: '.b-pricing-cols',
    oldPrice: '.b-pricing-cols__old-price',
    price: '.b-pricing-cols__price'
}

const classes = {
    oldPriceActual: 'b-pricing-cols__old-price_status_actual',
    hide: 'u-hide'
}

const dataAttributes = {
    countdown: 'countdown-time'
}

export default function initPricingCols() {


    $(selectors.main).each((index, elem) => {
        const $elem = $(elem);

        const elems = {
            $main: $elem,
            $oldPrice: $elem.find(selectors.oldPrice),
            $price: $elem.find(selectors.price)
        }

        const countdownTime = elems.$main.data(dataAttributes.countdown)


        if(countdownTime) {
            const baseTimeSpan = calcCountdownSpan(countdownTime);
            if(baseTimeSpan.value > 0) {
                const countdownInterval = setInterval(() => {
                    const timeSpan = calcCountdownSpan(countdownTime);
                    if(timeSpan.value <= 0) {
                        stopSale(elems);
                        clearInterval(countdownInterval)
                    }
                }, 1000)
            } else {
                stopSale(elems);
            }
        }

    })
}

function calcCountdownSpan(countDownTime) {
    return CountdownTimer(null, new Date(countDownTime))
}

function stopSale($elems) {
    $elems.$oldPrice.addClass(classes.oldPriceActual)
    $elems.$price.addClass(classes.hide)
}