import Popup from "../b-popup/b-popup";

export default function initFaq() {
    const selectors = {
        main: '.b-faq',
        item: '.b-faq__item',
        label: '.b-faq__label-text',
        text: '.b-faq__text',
        counter: '.b-faq__counter',
        popupCounter: '.b-faq-popup__counter',
        popupLabel: '.b-faq-popup__label',
        popupText: '.b-faq-popup__text'
    }

    const classes = {
        jsInit: 'js-dynamic-popup'
    }

    function init($faqList){
        const elements = {
            $items: $faqList.find(selectors.item),
            $popup: $(`#${$faqList.data('popup-target')}`)
        }

        elements.$items.click((e) => {
            const $item = $(e.currentTarget);
            const itemCounter = $item.find(selectors.counter).text().trim()
            const itemLabel = $item.find(selectors.label).text().trim()
            const itemText = $item.find(selectors.text).text().trim()

            elements.$popup.find(selectors.popupCounter).text(itemCounter)
            elements.$popup.find(selectors.popupLabel).text(itemLabel)
            elements.$popup.find(selectors.popupText).text(itemText)
            Popup.open(elements.$popup);
        })
    }

    $(selectors.main).each((index, elem) => {
        const $faqList = $(elem);
        if($faqList.hasClass(classes.jsInit)) {
           init($faqList);
        }
    })
}