export default function reviewFormInit() {
    const selectors = {
        main: '.b-review-form',
        inputFile: '.b-review-form__foto-input',
        btnFoto: '.b-review-form__btn_foto',
        btnFotoLabel: '.b-review-form__btn-label_foto'
    };

    const btnLabel = 'добавить фото';

    $(`${selectors.main}.js-init`).each((index, elem) => {
        const $elem = $(elem);
        const elems = {
            $main: $elem,
            $inputFile: $elem.find(selectors.inputFile),
            $btnFoto: $elem.find(selectors.btnFoto),
            $btnFotoLabel: $elem.find(selectors.btnFotoLabel)
        };

        elems.$inputFile.change(() => {
            elems.$btnFotoLabel.text(
                elems.$inputFile[0].files[0] !== undefined ?
                    elems.$inputFile[0].files[0].name
                    : btnLabel
            );
        })

    })
}
