import CountdownTimer from 'countdown';

export default class Countdown{
    static get selectors() {
        return {
            main: '.b-countdown',
            hour: '.b-countdown__number_hour',
            minute: '.b-countdown__number_minute',
            second: '.b-countdown__number_second',
        }
    }

    static init($context = $(document), options = {}){
        $(Countdown.selectors.main, $context).each((i, elem) => {
            const $elem = $(elem);

            if(!$elem.data('date-end')) return;
            if($elem.data('block-api')) return;

            new Countdown($elem, options);
        })
    }

    constructor($elem, options) {
        this.params = $.extend({
            selectors: Countdown.selectors,
        }, options);

        this.elems = {
            $main: $elem,
            $hour: $elem.find(this.params.selectors.hour),
            $minute: $elem.find(this.params.selectors.minute),
            $second: $elem.find(this.params.selectors.second)
        };

        this.dateEnd = this.elems.$main.data('date-end');

        this.initCountdown();

        this.initBlockApi();
    }

    initBlockApi(){
        this.elems.$main.data('block-api', this);
    }

    initCountdown() {
        const timespanInit = CountdownTimer(null, new Date(this.dateEnd));
        if(timespanInit.value < 0) return;

        this.renderTime(timespanInit);

        const interval = setInterval(() => {
            const timeOver = CountdownTimer(null, new Date(this.dateEnd));
            if(timeOver.seconds === 1 && timeOver.value < 0) {
                clearInterval(interval);
            } else {
                this.renderTime(timeOver);
            }
        }, 1000)
    }

    renderTime(timestamp){
        const hours = timestamp.hours + timestamp.days * 24;
        this.elems.$hour.text(hours.toLocaleString(undefined,{minimumIntegerDigits: 2}));
        this.elems.$minute.text(timestamp.minutes.toLocaleString(undefined,{minimumIntegerDigits: 2}));
        this.elems.$second.text(timestamp.seconds.toLocaleString(undefined,{minimumIntegerDigits: 2}));
    }

}