const socialSubscriptionInit = () => {

    const SOCIAL_NAMES = {
        VK: 'vk'
    };

    const selectors = {
        main: '.b-social-subscription',
        widgetContainer: '.b-social-subscription__widget-container'
    };

    $(selectors.main).each((i, elem) => {
        init($(elem));
    });

    function init($elem) {

        const $elems = {
            main: $elem,
            widgetContainer: $elem.find(selectors.widgetContainer)
        };

        const socialName = $elems.main.data('social-name');
        if (!socialName) return;

        const inlineConfig = $elems.main.data('config') || {};
        const inlineOptions = $elems.main.data('options') || {};

        switch (socialName) {
            case SOCIAL_NAMES.VK:
                initVk({inlineConfig, $elems, inlineOptions});
                break;
            default:
                return null;
        }

    }

    function initVk({ inlineConfig, $elems, inlineOptions = {} }) {

        const options = {
            scriptUrl: 'https://vk.com/js/api/openapi.js?161',
            nodeId: 'vk_groups',
            groupId: inlineOptions.groupId || '172926062'
        };

        const config = {
            height: "400",
            width: "auto",
            color3: "aa8368",
            ...inlineConfig
        };

        const useApi = () => {
            const vkApi = window.VK;
            vkApi.Widgets.Group(options.nodeId, config, options.groupId);
        };

        $elems.widgetContainer.attr('id', options.nodeId);

        if(typeof(window.VK) !== "undefined"){
            useApi();
        } else {
            const script = document.createElement('script');
            $(script).on('load', () => {
                useApi()
            });
            $(script).attr('src', options.scriptUrl);
            document.head.appendChild(script);
        }

    }
};

export default socialSubscriptionInit;
