export default class Steps{
    static get selectors() {
        return {
            main: '.b-steps',
            item: '.b-steps__item',
            stepImg: '.b-steps__steps-img',
            stepImgHovereble: '.b-steps__steps-img_hovereble',
        }
    }

    static get classes() {
        return {
            stepImgHidden: 'b-steps__steps-img_hidden',
            itemHovered: 'b-steps__item_hovered'
        }
    }

    static init($context = $(document), options = {}) {
        $(Steps.selectors.main, $context).each((i, elem) => {
            const $elem = $(elem);

            if(!$elem.hasClass('js-init')) return;
            if($elem.data('block-api')) return;

            new Steps($elem, options);
        })
    }

    constructor($elem, options) {
        this.params = $.extend({
            selectors: Steps.selectors,
            classes: Steps.classes
        }, options);

        this.elems = {
            $main: $elem,
            $item: $elem.find(this.params.selectors.item),
            $stepImgHovereble: $elem.find(this.params.selectors.stepImgHovereble)
        };

        this.handleItemHover();

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    activateHoverebleImgsByIndex(index){
        this.elems.$stepImgHovereble.each((i, elem) => {
            const $elem = $(elem);
            const stepsArray = $elem.data('hover-step');
            if(stepsArray.find(step => step === index)){
                $elem.removeClass(this.params.classes.stepImgHidden);
            } else {
                $elem.addClass(this.params.classes.stepImgHidden);
            }
        })
    }

    disactivateHoverebleImgsByIndex(index){
        this.elems.$stepImgHovereble.each((i, elem) => {
            const $elem = $(elem);
            const stepsArray = $elem.data('hover-step');
            if(stepsArray.find(step => step === index)){
                $elem.addClass(this.params.classes.stepImgHidden);
            }
        })
    }

    handleItemHover() {
        this.elems.$item.mouseover((e) => {
            const $target = $(e.currentTarget);
            const index = this.elems.$item.index($target) + 1;

            $target.addClass(this.params.classes.itemHovered);
            this.activateHoverebleImgsByIndex(index);
        });

        this.elems.$item.mouseleave((e) => {
            const $target = $(e.currentTarget);
            const index = this.elems.$item.index($target) + 1;

            $target.removeClass(this.params.classes.itemHovered);
            this.disactivateHoverebleImgsByIndex(index);
        })
    }

}
