import 'select2/dist/js/select2.js';
import 'tooltipster/dist/js/tooltipster.bundle.min.js';
import 'tooltipster-follower/dist/js/tooltipster-follower.min.js';

export default class Tools {
    static initSelect2() {
        function formatStateSocials (state) {
            if (!state.id) {
                return state.text;
            }
            const iconMod =`b-input-select__icon-holder_icon_${state.element.value}`
            const $state = $(
                '<div class="b-input-select__social-holder"><div class="b-input-select__icon-holder ' + iconMod + '"></div> ' + state.text + '</div>'
            );
            return $state;
        };

        function switchTemplateResultThemes($selectElem) {
            const templateResult = $selectElem.data('options').templateResult;

            switch (templateResult) {
                case 'socials':
                    return formatStateSocials;
                default:
                    return;
            }
        }

        function switchTemplateSelectionThemes($selectElem) {
            const templateResult = $selectElem.data('options').templateResult;

            switch (templateResult) {
                case 'socials':
                    return formatStateSocials;
                default:
                    return;
            }
        }
        $('.js-select').each(function () {
            const $select = $(this);
            const params = $(this).data('params');
            const inlineOptions = $(this).data('inline-options');

            $(this).select2({
                templateResult: switchTemplateResultThemes($select),
                templateSelection: switchTemplateSelectionThemes($select),
                ...params,
                ...inlineOptions
            });

            const $this = $(this);

            $('.select2-selection').keydown((e) => {
                if(e.key === 'Enter'){
                    $this.select2('close')
                }
            })
        });


    }

    static initTooltipster() {
        $('.js-tooltip').each(function () {
            let params = $(this).data();

            if (!params.plugins) params.plugins = ['follower'];
            if (!params.theme) params.theme = 'u-tooltip';
            if (!params.anchor) params.anchor = 'bottom-center';
            if (!params.offset) params.offset = [0, 15];
            if (!params.delay) params.delay = 100;

            $(this).tooltipster(params);
        });
    }

    static initYouTubeApi(){
        const tag = document.createElement('script');

        tag.src = "https://www.youtube.com/iframe_api";
        const firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
    }

    static closePopup($popup = $(`.b-popup_visible`)){
        $popup.removeClass('b-popup_visible');
        $('.b-page').removeClass('b-page_popup-visible');
    }

    static openPopup(popupId){
        const $popup = $(`.b-popup#${popupId}`);
        $popup.addClass('b-popup_visible');
        $('.b-page').addClass('b-page_popup-visible');
    }

    static getCookie(name){
        let matches = document.cookie.match(new RegExp(
            "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
        ));

        return matches ? decodeURIComponent(matches[1]) : null;
    }

    static setCookie(name, value, options = {}){
        let expires = options.expires;

        if (typeof expires == 'number' && expires) {
            let date = new Date();

            date.setTime(date.getTime() + (expires * 1000));
            expires = options.expires = date;
        }

        if (expires && expires.toUTCString) {
            options.expires = expires.toUTCString();
        }

        value = encodeURIComponent(value);

        let updatedCookie = name + '=' + value;

        for (let propName in options) {

            updatedCookie += '; ' + propName;
            let propValue = options[propName];

            if (propValue !== true) {
                updatedCookie += '=' + propValue;
            }
        }

        document.cookie = updatedCookie;
    }

    static deleteCookie(name){
        Tools.setCookie(name, '', {expires: -1});
    }

    static decorateNumber(number){
        return number.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ')
    }

}

export let ButtonPreloader = {

    buttons: [],

    showPreloader: function (button_identifier) {

        let btn = $(button_identifier);

        btn.css('pointer-events', 'none');

        this.buttons[button_identifier] = btn.html();

        btn.html(
            '<div class="wrapper-preloader">' +
            '<div class="circle circle-1"></div>' +
            '<div class="circle circle-1a"></div>' +
            '<div class="circle circle-2"></div>' +
            '<div class="circle circle-3"></div>' +
            '</div>'
        )
    },

    hidePreloader: function (button_identifier) {

        let btn = $(button_identifier);

        btn.css('pointer-events', 'auto');

        btn.html(this.buttons[button_identifier]);

        this.buttons.splice(button_identifier, 1);

    },

};

export function declinationNumber(number, titles) {
    const decCases = [2, 0, 1, 1, 1, 2];
    return number % 100 > 4 && number % 100 < 20 ? titles[2] : titles[decCases[Math.min(number % 10, 5)]];
}
