export default function initDropdownsList() {
    const selectors = {
        main: '.b-dropdowns-list',
        header: '.b-dropdowns-list__header',
        item: '.b-dropdowns-list__item'
    }

    const classes = {
        itemOpen: 'b-dropdowns-list__item_open'
    }

    $(selectors.main).each((index, elem) => {
        const $elem = $(elem);

        const elems = {
            $main: $elem,
            $header: $elem.find(selectors.header),
            $item: $elem.find(selectors.item)
        }

        elems.$header.click((e) => {
            $(e.currentTarget).closest(elems.$item).toggleClass(classes.itemOpen)
        })
    })
}