import {tns} from 'tiny-slider/src/tiny-slider'

export default class PlatesSlider {
    static get selectors() {
        return {
            main: '.b-plates-slider',
            slider: '.b-plates-slider__slider',
            btnPrev: '.b-plates-slider__arrow_prev',
            btnNext: '.b-plates-slider__arrow_next'
        }
    }

    static get dataSelectors() {
        return {
            inlineOptions: 'inline-options',
            blockApi: 'block-api'
        }
    }

    static get defaultSliderOptions() {
        return {
            items: 3,
            speed: 500,
            nav: false,
            responsive: {
                853: {
                    items: 3
                },
                600: {
                    items: 2
                },
                1: {
                    items: 1
                }
            }
        }
    }

    static init($context = $(document), options) {
        $(PlatesSlider.selectors.main, $context).each((index, elem) => {
            const $elem = $(elem);

            if($elem.data(PlatesSlider.dataSelectors.blockApi)) return;

            new PlatesSlider($elem, options);
        })
    }
    constructor($elem, options) {
        this.params = $.extend({
            selectors: PlatesSlider.selectors,
            dataSelectors: PlatesSlider.dataSelectors,
        }, options)

        this.elems = {
            $main: $elem,
            $slider: $elem.find(this.params.selectors.slider),
            $btnPrev: $elem.find(this.params.selectors.btnPrev),
            $btnNext: $elem.find(this.params.selectors.btnNext)
        }

        this.inlineSliderOptions = this.elems.$main.data(this.params.dataSelectors.inlineOptions) || {}

        this.initSlider()

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data(this.params.dataSelectors, this);
    }

    initSlider() {
        const sliderOptions = {
            ...PlatesSlider.defaultSliderOptions,
            container: this.elems.$slider.get(0),
            prevButton: this.elems.$btnPrev.get(0),
            nextButton: this.elems.$btnNext.get(0),
            ...this.inlineSliderOptions
        }

        this.slider = tns(sliderOptions)
    }
}