export default function headerInit() {
    const selectors = {
        main: '.b-header',
        selectCity: '.b-header__select-city',
        detectCity: '.b-header__detect-city',
        cityName: '.b-header__city-name'
    }

    $(selectors.main).each((i, elem) => {
        const $elem = $(elem);
        if(!$elem.hasClass('js-init')) return;

        initHeader($elem);
    })

    function initHeader($elem){
        const elems = {
            $main: $elem,
            $selectCity: $elem.find(selectors.selectCity),
            $detectCity: $elem.find(selectors.detectCity),
            $cityName: $elem.find(selectors.cityName)
        }

        elems.$cityName.click(() => {
            elems.$selectCity.fadeIn();       
        })

        $(document).mouseup(function (e) {
            if (elems.$selectCity.has(e.target).length === 0){
                elems.$selectCity.fadeOut();
            }
        })
    }
}