export default class Popup {
    static get selectors() {
        return {
            main: '.b-popup',
            oldPopup: '.popup',
            closeBtn: '.js-popup-close',
            openBtn: '.js-popup-open',
            body: '.b-page',
            inner: '.b-popup__inner',
            videoBlock: '.b-video',
        }
    }

    static get classes() {
        return {
            popupVisible: 'b-popup_visible',
            oldPopupVisible: 'is-visible',
            bodyPopupVisible: 'b-page_popup-visible',
        }
    }

    static get dataSelectors() {
        return {
            openTarget: 'popup',
            disableOuterClose: 'disable-outer-close',
            settings: 'settings',
            playerApi: 'player-api'
        }
    }

    static settingsMethods = {
        autoCall([delay, popupId]) {
            const target = $(`#${popupId}`);
            Popup.adjournedOpen(delay, target);
        }
    };

    static _initPopupClose() {
        $(Popup.selectors.closeBtn).click(() => {
            Popup.close();
        })

        $(document).mouseup((e) => {
            const $div = $(`.${Popup.classes.popupVisible} ${Popup.selectors.inner}`);
            if (!$div.is(e.target) &&
                $div.has(e.target).length === 0 &&
                !$(`.${Popup.classes.popupVisible}`).data(Popup.dataSelectors.disableOuterClose)) {
                    Popup.close();
            }
        })
    }

    static _initPopupOpen() {
        $(Popup.selectors.openBtn).click((e) => {
            const $target = $(e.currentTarget);
            const targetId = $target.data(Popup.dataSelectors.openTarget);
            if (!targetId) return;

            const targetSelector = `#${targetId}`;
            Popup.open($(targetSelector));
        });
    }

    static _applySettings($popup) {
        for (let method in $popup.data('settings')) {
            if (Popup.settingsMethods.hasOwnProperty(method)) {
                Popup.settingsMethods[method]($popup.data('settings')[method])
            }
        }
    }

    static adjournedOpen(delay, $popup) {
        setTimeout(() => {
            if($(`.${Popup.classes.popupVisible}, ${Popup.selectors.oldPopup}.${Popup.classes.oldPopupVisible}`).length > 0){
                Popup.adjournedOpen(delay, $popup)
            } else {
                Popup.open($popup)
            }
        }, delay)
    }

    static close($popup = $(`.${Popup.classes.popupVisible}`)) {
        $popup.removeClass(Popup.classes.popupVisible);
        $(Popup.selectors.body).removeClass(Popup.classes.bodyPopupVisible);

        const settings = $popup.data(Popup.dataSelectors.settings);
        if (settings) {
            if (settings.stopVideo) {
                const player = $popup.find(Popup.selectors.videoBlock).data(Popup.dataSelectors.playerApi);
                if (player) player.stopVideo();
            }
            if (settings.pauseVideo) {
                const player = $popup.find(Popup.selectors.videoBlock).data(Popup.dataSelectors.playerApi);
                if (player) player.pauseVideo();
            }
        }
    }

    static open($popup) {
        $popup.addClass(Popup.classes.popupVisible);
        $(Popup.selectors.body).addClass(Popup.classes.bodyPopupVisible);
    }

    static init() {
        Popup._initPopupClose();
        Popup._initPopupOpen();

        $(Popup.selectors.main).each((index, elem) => {
            if ($(elem).data('settings')) {
                Popup._applySettings($(elem));
            }
        })
    }
}