export default class ServicesBtns {
  static selectors = {
    main: '.b-service-btns',
    socialBtnsColumn: '.b-service-btns__soc-btns',
    callBtn: '.b-service-btns__btn-item_call',
    chatBtn: '.b-service-btns__btn-item_chat',
    socialBtns: 'a.b-service-btns__soc-btns-button'
  }

  static classes = {
    socBtnsColumnDisabled: 'b-service-btns__soc-btns_disabled',
    showAnimation: 'show-animation',
    hideAnimation: 'hide-animation',
    showAnimationMob: 'show-animation-mob',
    hideAnimationMob: 'hide-animation-mob',
    fullWidth: 'b-service-btns__btn-item_full-width',
  }

  static init($context = $(document), options = {}) {
    $(ServicesBtns.selectors.main).each((i, elem) => {
      const $elem = $(elem);

      new ServicesBtns($elem, options);
    })
  }

  constructor($elem, options) {
    this.params = $.extend({
      selectors: ServicesBtns.selectors,
      classes: ServicesBtns.classes
    }, options)

    this.elems = {
      $main: $elem,
      $socBtnsColumn: $elem.find(this.params.selectors.socialBtnsColumn),
      $chatBtn: $elem.find(this.params.selectors.chatBtn),
      $callBtn: $elem.find(this.params.selectors.callBtn),
      $socBtns: $elem.find(this.params.selectors.socialBtns),
    }

    this.minDesktopWidth = 853;

    this.setEvents();
  }

  setEvents() {
    let timer = null;

    if (this.getWindowWidth() >= this.minDesktopWidth) {
      this.elems.$main.mouseover(() => {
        this.showBtns();
        clearTimeout(timer);
      });
      this.elems.$main.mouseout(() => {
        timer = setTimeout(() => {
          this.hideBtns();
        }, 1000)
      });
    } else {
      this.elems.$chatBtn.click(() => this.showBtns());
      this.elems.$socBtnsColumn.click(() => this.hideBtns());
      this.elems.$socBtns.each((i, elem) => {
        const $elem = $(elem);
        $elem.click(ev => ev.stopPropagation());
      })
    }
  }

  showBtns() {
    if (this.getWindowWidth() >= this.minDesktopWidth) {
      this.elems.$callBtn.addClass(this.params.classes.fullWidth);
      setTimeout(() => {
        this.elems.$socBtnsColumn.removeClass(this.params.classes.socBtnsColumnDisabled);
        this.elems.$socBtns.each((i, elem) => {
          const $elem = $(elem);
          $elem.addClass(this.params.classes.showAnimation)
        });
      }, 200)
    } else {
      this.elems.$socBtnsColumn.removeClass(this.params.classes.socBtnsColumnDisabled);
      this.elems.$socBtnsColumn.addClass(this.params.classes.showAnimationMob);
    }
  }

  hideBtns() {
    if (this.getWindowWidth() >= this.minDesktopWidth) {
      this.elems.$socBtns.each((i, elem) => {
        const $elem = $(elem);
        $elem.removeClass(this.params.classes.showAnimation)
        $elem.addClass(this.params.classes.hideAnimation)
      });
      setTimeout(() => {
        this.elems.$socBtnsColumn.addClass(this.params.classes.socBtnsColumnDisabled);
        this.elems.$socBtns.each((i, elem) => {
          const $elem = $(elem);
          $elem.removeClass(this.params.classes.hideAnimation)
        });
        this.elems.$callBtn.removeClass(this.params.classes.fullWidth);
      }, 190)
    } else {
      this.elems.$socBtnsColumn.removeClass(this.params.classes.showAnimationMob);
      this.elems.$socBtnsColumn.addClass(this.params.classes.hideAnimationMob);
      setTimeout(() => {
        this.elems.$socBtnsColumn.addClass(this.params.classes.socBtnsColumnDisabled);
        this.elems.$socBtnsColumn.removeClass(this.params.classes.hideAnimationMob)
      }, 90)
    }
  }

  getWindowWidth() {
    return $(window).width();
  }
}