import Tools from '../../../resources/scripts/_tools';

export default class PartnerCalc {
    static get selectors() {
        return {
            main: '.b-partner-calc',
            rail: '.b-partner-calc__slider-rail',
            walker: '.b-partner-calc__walker',
            walkerValue: '.b-partner-calc__walker-value',
            minNumber: '.b-partner-calc__slider-number_start',
            maxNumber: '.b-partner-calc__slider-number_end',
            startEdgeIcon: '.b-partner-calc__edge-icon',
            sliderImg: '.b-partner-calc__slider-img',
            progressBar: '.b-partner-calc__slider-progress',
            summaryPrice: '.b-partner-calc__summary-text',
            serviceWalk: '.js-service-walk',
            serviceSitting: '.js-service-sitting',
            serviceNanny: '.js-service-nanny',
        }
    }

    static get dataSelectors() {
        return {
            value: 'value',
            config: 'config'
        }
    }

    static get config() {
        return {
            minNumber: 100,
            maxNumber: 1000,
            clientPrice: 2788,
            clientConversion: 25,
            conversionWalk: 30,
            conversionSitting: 20,
            conversionNanny: 25,
        }
    }

    static init($context = $(document), options = {}) {
        $(PartnerCalc.selectors.main, $context).each((i, elem) => {
            const $elem = $(elem);

            if(!$elem.hasClass('js-init')) return;
            if($elem.data('block-api')) return;

            new PartnerCalc($elem, options);
        })
    }

    constructor($elem, options) {
        this.params = $.extend({
            selectors: PartnerCalc.selectors,
            config: PartnerCalc.config
        }, options);

        this.elems = {
            $main: $elem,
            $rail: $elem.find(this.params.selectors.rail),
            $walker: $elem.find(this.params.selectors.walker),
            $walkerValue: $elem.find(this.params.selectors.walkerValue),
            $minNumber: $elem.find(this.params.selectors.minNumber),
            $maxNumber: $elem.find(this.params.selectors.maxNumber),
            $startEdgeIcon: $elem.find(this.params.selectors.startEdgeIcon),
            $progressBar: $elem.find(this.params.selectors.progressBar),
            $sliderImg: $elem.find(this.params.selectors.sliderImg),
            $summaryPrice: $elem.find(this.params.selectors.summaryPrice),
            $serviceWalk: $elem.find(this.params.selectors.serviceWalk),
            $serviceSitting: $elem.find(this.params.selectors.serviceSitting),
            $serviceNanny: $elem.find(this.params.selectors.serviceNanny),
        };

        const value = this.elems.$main.data(PartnerCalc.dataSelectors.value) ?
            JSON.parse(this.elems.$main.data(PartnerCalc.dataSelectors.value))
            : PartnerCalc.config.minNumber;

        const inlineConfig = this.elems.$main.data(PartnerCalc.dataSelectors.config) ?
            JSON.parse(this.elems.$main.data(PartnerCalc.dataSelectors.config))
            : {};

        this.params.config = {
            ...this.params.config,
            ...inlineConfig
        };

        this.state = {
            walkerPosition: 0,
            value
        };

        this.initStart();
        this.dragAndDrop();

        this.initBlockApi();
    }

    initBlockApi() {
        this.elems.$main.data('block-api', this);
    }

    initStart() {
        this.elems.$maxNumber.text(this.params.config.maxNumber);
        this.elems.$minNumber.text(this.params.config.minNumber);
        this.renderSummary();
    }

    dragAndDrop() {
        const isTouchDevice = 'ontouchstart' in document.documentElement;

        this.elems.$walker.on(isTouchDevice ? 'touchstart' : 'mousedown', (event) => {

            const coords = this.getCoords(this.elems.$walker);
            const shiftX = event.pageX - coords.left;

            $(document).on(isTouchDevice ? 'touchmove' : 'mousemove', (event) => {
                this.moveAt(event, shiftX);
            });

            this.elems.$walker.on(isTouchDevice ? 'touchend' : 'mouseup', () => {
                this.destroyDragAndDropEvents();
            })

        });

        this.rejectBaseDrag()
    }

    rejectBaseDrag() {
        const elems = [
            this.elems.$walker,
            this.elems.$startEdgeIcon,
            this.elems.$sliderImg
        ];

        elems.forEach(($elem) => {
            $elem.get(0).ondragstart = () => {
                return false;
            }
        })
    }

    destroyDragAndDropEvents() {
        const isTouchDevice = 'ontouchstart' in document.documentElement;

        $(document).unbind(isTouchDevice ? 'touchmove' : 'mousemove');
        this.elems.$walker.unbind(isTouchDevice ? 'touchend' : 'mouseup');
    }

    getCoords($elem) {
        return {
            left: $elem.offset().left,
            top: $elem.offset().top
        }
    }

    moveAt(event, shiftX){
        const { walkerPosition, value } = this.state;
        const maxPos = this.elems.$rail.width();
        const newPos = event.pageX - this.getCoords(this.elems.$walker).left - shiftX + walkerPosition;

        if(newPos >= 0 && newPos <= maxPos){
            this.moveWalker(newPos)
        } else {
            this.destroyDragAndDropEvents();
            newPos < 0 ?
                this.moveWalker(0)
                : this.moveWalker(this.elems.$rail.outerWidth())
        }

    }

    moveWalker(leftPos) {
        this.elems.$walker.css('left', `${leftPos}px`);
        this.state = {
            ...this.state,
            walkerPosition: leftPos
        }
        this.renderNewValue(leftPos);
    }

    renderNewValue(newPos) {
        const newValue = this.calculateNewValue(newPos);
        this.elems.$walkerValue.text(newValue.value);
        this.elems.$progressBar.css('width', `${newValue.percentPos}%`);
        this.state = {
            ...this.state,
            value: newValue.value
        }

        this.renderSummary();
    }

    calculateNewValue(newPos) {
        const {maxNumber, minNumber} = this.params.config;

        const percentPos = newPos / this.elems.$rail.outerWidth() * 100;
        const value = Math.trunc(percentPos / 100 * (maxNumber - minNumber) + minNumber);

        return { value, percentPos }
    }

    calculateService(serviceKey) {
        const { value } = this.state;
        return Math.trunc(this.params.config[serviceKey] / 100 * value)
    }

    calculateTotalPrice() {
        const { value } = this.state;
        return value * (this.params.config.clientConversion / 100) * this.params.config.clientPrice;
    }

    renderSummary() {
        this.elems.$summaryPrice.text(Tools.decorateNumber(this.calculateTotalPrice()));
        this.elems.$serviceWalk.text(Tools.decorateNumber(this.calculateService('conversionWalk')));
        this.elems.$serviceSitting.text(Tools.decorateNumber(this.calculateService('conversionSitting')));
        this.elems.$serviceNanny.text(Tools.decorateNumber(this.calculateService('conversionNanny')));
    }
}