const menuInit = ($context) => {
    const selectors = {
        main: '.b-menu',
        mobileMenu: '.b-menu__mobile-menu',
        mobileMenuBtn: '.js-menu-mobile-menu',
        mobileMenuBlock: '.b-mobile-menu',
        dropDownLink: '.b-dropdown-menu__link'
    }

    const classes = {
        fixed: 'b-menu_fixed',
        mobileMenuBlockActive: 'b-mobile-menu_visible',
        mobileMenuActive: 'b-menu__mobile-menu_visible',
        mobileMenuVisible: 'b-menu_mobile-visible',

    }

    function constructMenu($elem){
        const elems = {
            $main: $elem,
            $mobileMenuBtn: $elem.find(selectors.mobileMenuBtn),
            $mobileMenu: $elem.find(selectors.mobileMenu),
            $mobileMenuBlock: $elem.find(selectors.mobileMenuBlock),
            $dropDownLink: $elem.find(selectors.dropDownLink)
        }

        const menuElemOffset = elems.$main.offset();
        if(menuElemOffset){
            let menuOffset = menuElemOffset.top;
            $(document).scroll(function() {
                if ($(document).scrollTop() > menuOffset) {
                    elems.$main.addClass(classes.fixed);
                } else {
                    elems.$main.removeClass(classes.fixed);
                }
            });
        }

        elems.$mobileMenuBtn.click(() => {
            toggleMenu(elems);
        })
        elems.$dropDownLink.click(() => {
            toggleMenu(elems, 'close');
        })
    }

    function toggleMenu(elems, toggler = '') {
        switch (toggler) {
            case 'close':
                closeMenu(elems);
                break;
            case 'open':
                openMenu(elems);
                break;
            default:
                elems.$mobileMenuBlock.toggleClass(classes.mobileMenuBlockActive);
                elems.$mobileMenu.toggleClass(classes.mobileMenuActive);
                elems.$main.toggleClass(classes.mobileMenuVisible);
        }
    }

    function openMenu(elems){
        elems.$mobileMenuBlock.addClass(classes.mobileMenuBlockActive);
        elems.$mobileMenu.addClass(classes.mobileMenuActive);
        elems.$main.addClass(classes.mobileMenuVisible);
    }

    function closeMenu(elems){
        elems.$mobileMenuBlock.removeClass(classes.mobileMenuBlockActive);
        elems.$mobileMenu.removeClass(classes.mobileMenuActive);
        elems.$main.removeClass(classes.mobileMenuVisible);
    }


    $(selectors.main).each((i, elem) => {
        const $elem = $(elem);
        if(!$elem.hasClass('js-init')) return;

        constructMenu($elem);
    })
}


export default menuInit;
